/*********************************************** 
  Layout
***********************************************/
/* html and body
***********************************************/
html {
  font-size: 10px;
  font-family: "Open Sans", "Helvetica", "Verdana", "Arial", sans-serif;
}

body {
  font-size: 1.6rem;
  color: #0d0d0d;
  background: #fff;
}

/* Page content
***********************************************/
.content-fluid {
  margin-right: auto;
  margin-left: auto;
  /* Maximum width for fluid content */
  max-width: 1280px;
}

/* Responsive styles for content */
.content {
  box-sizing: border-box;
  /* Centrify */
  margin-right: auto;
  margin-left: auto;
  /* Responsive styles for content */
}

@media (min-width: 600px) {
  .content {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .content {
    width: 970px;
  }
}

@media (min-width: 1280px) {
  .content {
    width: 1120px;
  }
}

/*********************************************** 
  Grid system
***********************************************/
/* Grid wrapper
***********************************************/
.grid,
.grid-fluid,
.content,
.content-fluid {
  box-sizing: border-box;
  padding-right: 12px;
  padding-left: 12px;
}

.grid:before, .grid:after,
.grid-fluid:before,
.grid-fluid:after,
.content:before,
.content:after,
.content-fluid:before,
.content-fluid:after {
  content: '';
  display: table;
}

.grid:after,
.grid-fluid:after,
.content:after,
.content-fluid:after {
  clear: both;
}

/* Grid rows
***********************************************/
.row {
  margin-right: -12px;
  margin-left: -12px;
}

.row:before, .row:after {
  content: '';
  display: table;
}

.row:after {
  clear: both;
}

/* Grid row features
***********************************************/
/* Collapse row feature */
.row--collapse {
  margin-left: 0;
  margin-right: 0;
}

.row--collapse > [class*='col-'] {
  padding-left: 0;
  padding-right: 0;
}

